* {
  box-sizing: border-box; }

#calendario {
  position: relative; }
  #calendario .cambiar-mes {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 15px 0; }
    #calendario .cambiar-mes .flecha {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 48px !important;
      min-width: 48px !important;
      max-width: 48px !important;
      height: 48px !important;
      min-height: 48px !important;
      max-height: 48px !important;
      background-color: #eee;
      margin-right: 10px;
      -webkit-transition: all 150ms ease;
      -moz-transition: all 150ms ease;
      -ms-transition: all 150ms ease;
      -o-transition: all 150ms ease;
      transition: all 150ms ease;
      cursor: pointer; }
      #calendario .cambiar-mes .flecha:hover {
        background-color: #ccc; }
  #calendario .filtrar-red {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    flex-direction: column; }
    #calendario .filtrar-red select {
      min-width: 350px;
      border: 1px solid #ccc;
      border-radius: 4px;
      padding: 6px;
      margin-bottom: 10px;
      background-color: #fff; }
  #calendario .meses {
    width: 100%; }
    #calendario .meses .mes {
      display: flex;
      flex-direction: column; }
      #calendario .meses .mes .titulo {
        margin-bottom: 15px; }
        #calendario .meses .mes .titulo span {
          text-transform: uppercase;
          font-size: 130%;
          font-weight: bold; }
      #calendario .meses .mes .dias {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap; }
        #calendario .meses .mes .dias .dia {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 13.8vw;
          min-width: 13.8vw;
          height: 18vh;
          position: relative;
          -webkit-box-shadow: 0 0 0 1px #ccc;
          box-shadow: 0 0 0 1px #ccc;
          -webkit-transition: all 150ms ease;
          -moz-transition: all 150ms ease;
          -ms-transition: all 150ms ease;
          -o-transition: all 150ms ease;
          transition: all 150ms ease; }
          #calendario .meses .mes .dias .dia.old {
            background-color: #eee; }
          #calendario .meses .mes .dias .dia.today {
            background-color: #ffa; }
          #calendario .meses .mes .dias .dia .numero {
            position: absolute;
            left: 3px;
            top: 3px; }
            #calendario .meses .mes .dias .dia .numero hora_pub span {
              font-size: 80%; }
          #calendario .meses .mes .dias .dia .lista-posts {
            width: 100%;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column; }
            #calendario .meses .mes .dias .dia .lista-posts .post {
              width: 100%;
              position: relative; }
              #calendario .meses .mes .dias .dia .lista-posts .post .info {
                font-size: 90%;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                padding: 1px 3px;
                margin: 1px 0;
                width: 100%;
                background-color: #255533;
                color: #fff;
                cursor: pointer;
                -webkit-transition: all 150ms ease;
                -moz-transition: all 150ms ease;
                -ms-transition: all 150ms ease;
                -o-transition: all 150ms ease;
                transition: all 150ms ease; }
                #calendario .meses .mes .dias .dia .lista-posts .post .info:hover {
                  background-color: #279948; }
                #calendario .meses .mes .dias .dia .lista-posts .post .info .hora {
                  margin-right: 5px; }
  #calendario .make-new-post {
    position: fixed;
    right: 20px;
    bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px !important;
    min-width: 60px !important;
    max-width: 60px !important;
    height: 60px !important;
    min-height: 60px !important;
    max-height: 60px !important;
    -webkit-box-shadow: 0 0 4px 0 #444;
    box-shadow: 0 0 4px 0 #444;
    cursor: pointer;
    border-radius: 50%;
    background-color: #43ab49; }



