* {
  box-sizing: border-box; }

.card-post {
  position: absolute;
  left: 100%;
  top: 100%;
  background-color: #fff;
  -webkit-box-shadow: 0 0 3px 0 #444;
  box-shadow: 0 0 3px 0 #444;
  z-index: 100;
  padding: 15px;
  display: flex;
  flex-direction: column;
  min-width: 40vw;
  cursor: auto; }
  .card-post .cerrar-popup {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer; }
  .card-post .pubdate span {
    color: #666;
    font-size: 90%; }
  .card-post .texto {
    max-height: 200px;
    overflow: auto; }
  .card-post .imagen {
    width: 100%;
    height: 300px; }
    .card-post .imagen img {
      width: 100%;
      height: 100%;
      object-fit: contain; }



